/* VARIABLES */

:root {
  --main-color: #1a8fff;
  --text-color: #333;
  --text-color-light: #ccc;
  --border-color: #0575e6;
  --bg-color: #f9f9f9;
  --neutral-color: #fff;
  --misc: #021b79;
}


/* GENERAL */

* {
  box-sizing: border-box;
}

body {
    font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5;
    color: #333;
    color: var(--text-color);
    background: #f9f9f9;
    background: var(--bg-color);
    position: relative;
}

header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid #0575e6;
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  background: var(--neutral-color);
}

header #logo {
  font-size: 175%;
  text-align: center;
  color: #1a8fff;
  color: var(--main-color);
  line-height: 1;
}

header #logo .icon {
  padding-right: .25em;
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 5em;
  max-width: 50em;
}
 .hover{
   cursor: pointer;
  -webkit-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
 }
.hover:hover{
  text-decoration:underline ;
  color:#1a8fff;
  color:var(--main-color);
}
/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  -webkit-align-items: center;
          align-items: center;
}

.col {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-basis: 0;
          flex-basis: 0;
  max-width: 100%;
}

.col-start {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-align: left;
}

.col-center {
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.col-end {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  text-align: right;
}
